import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { userRole } from 'src/app/core/constants/constants';
import { loginUrls } from './../core/constants/login-constants';
import { AuthService } from './../core/service/auth/auth.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
@Injectable ({
  providedIn: 'root'
})
export class RoleGuard implements CanActivate {
  constructor(

    private _auth: AuthService,
    private router: Router,
    private _helper: FunctionService) { }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {

    if (this._auth.isLoggedIn) {
      const user = this._helper.getUser;
      if (user?.user_type === userRole.employee || user?.user_type === userRole.admin || user?.user_type === userRole.manager) {
        return true;
      } else {
        this.router.navigate ([loginUrls.logout], {queryParams: { returnUrl: state.url}});
      }
    } else {
      this.router.navigate ([loginUrls.logout], {queryParams: { returnUrl: state.url}});
    }
    return false;
  }

}
