import { iconRender } from './../../interface/common.interface';
import { Component, Input, OnInit, EventEmitter, Output } from '@angular/core';

@Component ({
  selector: 'app-icon-render',
  templateUrl: './icon-render.component.html',
  styleUrls: ['./icon-render.component.scss']
})
export class IconRenderComponent implements OnInit {
  @Input () value: iconRender;

  @Output () customEvent = new EventEmitter ();

constructor() { }

ngOnInit(): void {
  }

  iconClick(){
    this.customEvent.emit (this.value);
  }
}
