import { CommonApiService } from 'src/app/core/service/common/common-api.service';
import { SubSink } from 'subsink';
import { Component, OnInit, OnDestroy, OnChanges, Input } from '@angular/core';
@Component ({
  selector: 'app-iframe-loader',
  templateUrl: './iframe-loader.component.html',
  styleUrls: ['./iframe-loader.component.scss']
})
export class IframeLoaderComponent implements OnInit , OnDestroy , OnChanges  {
  @Input () iframeUrl: string;

  @Input () height = '1618px';

  @Input () width = '100%';

  public budgetTableau: any;

  public customStyle = {
    'width': '100%',
    'height': '1618px',
    'border': 'none'
  };

  public budgetAvailable = false;

  private subs = new SubSink ();

  constructor(private _commonApi:  CommonApiService) {
  }

  ngOnChanges(): void {
    this.customStyle.width = this.width;
    this.customStyle.height = this.height;
    if (this.iframeUrl) {
      this.getTicket ();
    }
  }

  ngOnInit(): void {
  }

  getTicket() {
    this.subs.add (
      this._commonApi.getTableauTicket ().subscribe ( res => {
        this.budgetTableau = `https://ask.beinex.com/trusted/${res['iframe_ticket']}/${this.iframeUrl}?:embed=yes&:toolbar=no`;
      this.budgetAvailable = true;
  }));
  }

  ngOnDestroy() {
    this.subs.unsubscribe ();
    }
  }
