import { Status } from './../interface/common.interface';
import { toastMessages } from './../constants/warning-messages';
import { LoginService } from './../service/login/login.service';
import { ApiStatus, autoStatusExcludedApiUrls } from './../constants/constants';
import { ToastService } from 'src/app/core/helper/toast.helper.service';
import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';
import { of } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { CookieService } from 'ngx-cookie-service';

@Injectable ()
export class StatusCheckInterceptor implements HttpInterceptor {

  constructor(private _toast: ToastService, private _loginApi: LoginService, private _cookie: CookieService) { }

intercept(request: HttpRequest<any>, next: HttpHandler) {
    let excludeUrl = false;
    autoStatusExcludedApiUrls.map ( urls => {
       if ( request.url?.includes (urls)) {
        excludeUrl = true;
       }
    });
    return next.handle (request).pipe (
      tap (evt => {
        if (evt instanceof HttpResponse) {
          if (evt.url.includes ('.json') || evt.url.includes ('selected_fields_list')) {
          } else {
            evt.body.status = evt.status;
          }
        }
      }),
      catchError ((err) => {
        if (!excludeUrl) {
            this.showHttpErrorToast (err);
        } else {
          if (err?.status===  ApiStatus.corsOrigin) {
            this.showHttpErrorToast (err);
          } else {
            err.error.status = err.status;
            return of (new HttpResponse ({ body: err.error}));
          }
        }
          return of (err);
        }
      )
    );
  }

showHttpErrorToast(err) {
    if (err instanceof HttpErrorResponse) {
      const status = err.status;
      switch (status) {
        case ApiStatus.serverDown:
          this.showMessage (err);
          break;
        case ApiStatus.unAuthorized: {
            this._loginApi.forceLogout ();
            this.showMessage (err);
            break;
          }
        case ApiStatus.forbidden: {
            this._loginApi.forceLogout ();
            this.showMessage (err);
            break;
          }
          case ApiStatus.badRequest: {
            if (err.error instanceof Array) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Array) {
                  errors[el].forEach (error => {
                    this._toast.warning (el + '  : ' + error);
                  });
                }
              });
            } else if (err.error instanceof Object) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Object) {
                  const subKey = Object.keys (errors[el]);
                  subKey.forEach (fieldName => {
                    this._toast.warning (fieldName + '  : ' + errors[el][fieldName]);
                  });
                }
              });
             } else {
              this.showMessage (err);
            }
            break;
          }
        case ApiStatus.notFound: {
            this.showMessage (err);
            break;
          }
        case ApiStatus.timeOut: {
            this._toast.error (toastMessages.gateWayTimeOutToast);
            break;
          }
          case ApiStatus.corsOrigin: {
            this._toast.error (toastMessages.errorMessageAdmin);
            break;
          }
          case ApiStatus.conflict: {
            if (err.error instanceof Array) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Array) {
                  errors[el].forEach (error => {
                    this._toast.warning (el + '  : ' + error);
                  });
                } else {
                  this.showMessage (err);
                }
              });
            } else if (err.error instanceof Object) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Object) {
                  const subKey = Object.keys (errors[el]);
                  subKey.forEach (fieldName => {
                    this._toast.warning (fieldName + '  : ' + errors[el][fieldName]);
                  });
                } else {
                  this.showMessage (err);
                }
              });
             } else {
              this.showMessage (err);
            }
            break;
          }
          case ApiStatus.unprocessableEntity : {
            if (err.error instanceof Array) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Array) {
                  errors[el].forEach (error => {
                    this._toast.warning (el + '  : ' + error);
                  });
                } else {
                  this.showMessage (err);
                }
              });
            } else if (err.error instanceof Object) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Object) {
                  const subKey = Object.keys (errors[el]);
                  subKey.forEach (fieldName => {
                    this._toast.warning (fieldName + '  : ' + errors[el][fieldName]);
                  });
                } else {
                  this.showMessage (err);
                }
              });
             } else {
              this.showMessage (err);
            }
            break;
          }
          case ApiStatus.failedDependency: {
            if (err.error instanceof Array) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Array) {
                  errors[el].forEach (error => {
                    this._toast.warning (el + '  : ' + error);
                  });
                } else {
                  this.showMessage (err);
                }
              });
            } else if (err.error instanceof Object) {
              const key = Object.keys (err.error);
              const errors = err.error;
              key.forEach (el => {
                if (errors[el] instanceof Object) {
                  const subKey = Object.keys (errors[el]);
                  subKey.forEach (fieldName => {
                    this._toast.warning (fieldName + '  : ' + errors[el][fieldName]);
                  });
                } else {
                  this.showMessage (err);
                }
              });
             } else {
              this.showMessage (err);
            }
            break;
          }
        default:
          {
            this.showMessage (err);
            break;
          }
      }
    }
  }

showMessage(err) {
    if (err.error['detail']) {
      this._toast.warning (err.error['detail']);
    } else if (err.error['details']) {
      if (err.error['details']?.email) {
        err.error['details']?.email.forEach (notification => {
          this._toast.warning (notification);
        });
      } else if (err.error['details']?.nodata) {
        err.error['details']?.nodata.forEach (notification => {
          this._toast.warning (notification);
        });
      } else if (err.error['details']?.name) {
        err.error['details']?.name.forEach (notification => {
          this._toast.warning (notification);
        });
      } else if (err.error['details']?.non_field_errors) {
        err.error['details']?.non_field_errors.forEach (notification => {
          this._toast.warning (notification);
        });
      } else if (err.error['details']?.budget) {
        err.error['details']?.budget.forEach (category => {
          this._toast.warning (category);
        });
      } else if (err.error['details']?.sub_budget) {
        err.error['details']?.sub_budget.forEach (subCategory => {
          this._toast.warning (subCategory);
        });
      }
    } else if (err.error['message']) {
      this._toast.warning (err.error['message']);
    } else if (err.error['msg']) {
      this._toast.warning (err.error['msg']);
    }  else if (err.error['error']) {
      this._toast.warning (err.error['error']);
    } else {
      this._toast.networkError ();
    }
  }
}
