import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { Component, OnInit, OnDestroy, Input, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component ({
  selector: 'app-modal-basic',
  templateUrl: './modal-basic.component.html',
  styleUrls: ['./modal-basic.component.scss']
})
export class ModalBasicComponent implements OnInit, OnDestroy {
  @ViewChild ('modal') modal;

  @Input () center = false;

  @Input () isSmallModal = false;

  @Input () dialogClass: string;

  @Input () hideHeader = false;

  @Input () hideFooter = false;

  @Input () hideBody = false;

  @Input () disableOutSideClick = false;

  public config: PerfectScrollbarConfigInterface = {};

  public visible = false;

  public visibleAnimate = false;

constructor(private modalService: NgbModal) { }

ngOnInit() {

  }

public show(): void {
    this.visible = true;
    setTimeout (() => this.visibleAnimate = true, 100);
    if (!this.isSmallModal){
      this.modalService.dismissAll ();
      this.modalService.open (this.modal, { size: 'lg', centered: this.center, scrollable: true });

    } else {
      this.modalService.dismissAll ();
      this.modalService.open (this.modal, { size: '', centered: this.center, scrollable: true });

    }
  }

public hide(): void {
    this.visibleAnimate = false;
      this.visible = false;
      this.modalService.dismissAll ();
  }

public onContainerClicked(event: MouseEvent): void {
    if ((<HTMLElement>event.target).classList.contains ('modal')) {
      if (!this.disableOutSideClick) {
        this.hide ();
      }
    }
  }

  ngOnDestroy() {
    this.hide ();
  }

}
