import { FunctionService } from './../core/helper/function.helper.service';
import { userRole } from './../core/constants/constants';
import { loginUrls } from './../core/constants/login-constants';
import { AuthService } from './../core/service/auth/auth.service';
import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';

@Injectable ({
  providedIn: 'root'
})
export class PublicAuthGuard implements CanActivate {
  constructor(private router: Router, private _auth: AuthService, private _helper: FunctionService) { }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
      const user = this._helper.getUser;
    if (this._auth.isLoggedIn) {
      if (user  === userRole.admin) {
        this.router.navigate ([loginUrls.dashboard]);
      } else {
        this.router.navigate ([  loginUrls.employee]);
      }
      if (this._auth.isAuth !== '') {
        if (user  === userRole.admin) {
          this.router.navigate ([loginUrls.dashboard]);
        } else {
          this.router.navigate ([  loginUrls.employee]);
        }
      } else {
        this.router.navigate (['/logout']);
      }
    }
    return true;
  }

}
