import { toastMessages } from './../../core/constants/warning-messages';
import { ValidationData } from './../create-password/create-password.component';
import { returnUrl, loginUrls, loginValue } from './../../core/constants/login-constants';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { environment } from 'src/environments/environment';
import { LoaderService } from 'src/app/core/service/loader/loader.service';
import { ValidationruleService } from './../../core/helper/validationrule.service';
import { SubSink } from 'subsink';
import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { ApiStatus, LocalStorageKey, userRole } from 'src/app/core/constants/constants';
import { StorageService } from 'src/app/core/helper/storage.service';
import { LoginService } from 'src/app/core/service/login/login.service';
import { ToastService } from 'src/app/core/helper/toast.helper.service';
import { CookieService } from 'ngx-cookie-service';
import { DOCUMENT } from '@angular/common';

@Component ({
  selector: 'app-login',
  styleUrls: ['./login.component.scss'],
  templateUrl: './login.component.html'
})
export class LoginComponent implements OnInit, OnDestroy {
  public  loginForm = true;

  public recoverForm = false;

  public passwordClick = false;

  public validationPassword = true;

  public registerForm!: FormGroup;

  public resetForm!: FormGroup;

  public submitted = false;

  public resetFormSubmitted = false;

  public resetEmail!: string;

  public httpError!: string;

  public httpErrorForgot!: string;

  public loading = false;

  private subs = new SubSink ();

  private userDetails = {};

  constructor(
    @Inject (DOCUMENT) private document: Document,
    private formBuilder: FormBuilder,
    private rule: ValidationruleService,
    public login: LoginService,
    private storage: StorageService,
    private _loader: LoaderService,
    private _helper: FunctionService,
    private _toaster: ToastService,
    private router: Router,
    private _activatedRoute: ActivatedRoute) {
    this._loader.isLoading.subscribe (res => {
      this.loading = res;
    });
  }

  ngOnInit() {
    this.httpError = '';
    this.httpErrorForgot = '';
   this.setForm ();
    this.resetForm = this.formBuilder.group ({
      email: ['', this.rule.emailValidation]
    });
  }

setForm() {
  this.registerForm = this.formBuilder.group ({
    email: ['', this.rule.emailValidation],
    password: ['', this.rule.requiredValidation],
  });
}

  get f() {
 return this.registerForm.controls;
}

  get resetFormControl() {
 return this.resetForm.controls;
}

  showRecoverForm() {
    this.resetEmail = '';
    this.loginForm = !this.loginForm;
    this.resetFormSubmitted = false;
    this.submitted = false;
    this.passwordClick = false;
    this.httpError = '';
    this.httpErrorForgot = '';
    this.setForm ();
    this.resetForm.reset ();
    this.recoverForm = !this.recoverForm;
    if (this.recoverForm) {
      this.validationPassword = false;
    } else {
      this.validationPassword = true;
    }
  }

onSubmit() {
    this.submitted = true;
    document.cookie = `${LocalStorageKey.userLogin}=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;;Domain=.${environment.cookieDomain}`;

    // stop here if form is invalid
    if (this.registerForm.invalid) {
      return;
    }
    this.loading = true;
    this.subs.add (
      this.login.login (this.registerForm.value).subscribe ((res : any) => {
        if (res.status === ApiStatus.ok) {
          this.httpError = '';
          let returnUrlValue =  this._activatedRoute.snapshot.queryParamMap.get (returnUrl);
          if ( returnUrlValue?.includes (loginUrls.logout)) {
             returnUrlValue = null;
          }
          this.userDetails = res;
          this._helper.setUser (res);
          this._toaster.success (res?.message);
            if (res.user_type === userRole.admin) {
              this.router.navigate ([returnUrlValue || loginUrls.dashboard]);
            } else {
              this.router.navigate ([ returnUrlValue || loginUrls.employee]);
            }
        } else {
           if (res[loginValue.message]) {
            this.httpError = res[loginValue.message];
          } else if (res[loginValue.msg]) {
            this.httpError = res[loginValue.msg];
          }
        }
      })
    );
  }

getUserProfile() {
    this.subs.add (
      this.login.userProfile ().subscribe ((res : any) => {
        if (res.status === ApiStatus.ok) {
          if (res instanceof Array) {
            this.storage.setLocalStorage (LocalStorageKey.userDetails, res[0]);
          }
        }
      })
    );
  }

resetPassword() {
    this.resetFormSubmitted = true;
    this.resetEmail = this.resetForm.value.email;
    if (this.resetForm.invalid) {
      this._toaster.warning (toastMessages.invalidEmail);
      return;
    }
    const formData = new FormData ();
    formData.append (loginValue.email, this.resetEmail);
    this.subs.add (
      this.login.resetPassword (formData).subscribe ((res : any) => {
        if (res.status === ApiStatus.created) {
          this.httpErrorForgot = '';
          this._toaster.success (res.message);
          this.resetEmail = '';
          this.showRecoverForm ();
        } else {
          if (res[loginValue.message]) {
            this.httpErrorForgot = res[loginValue.message];
          } else if (res[loginValue.msg]) {
            this.httpErrorForgot = res[loginValue.msg];
          }
        }
      })
    );
  }

  ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}

