import { Injectable } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';

@Injectable ({
  providedIn: 'root'
})
export class ToastService {

  constructor(
    private _toaster: ToastrService,
    private _translate: TranslateService
  ) { }

  success(text = '') {
    this._toaster.success (this._translate.instant (text), '', {
      timeOut: 3000,
      closeButton: true,
      progressBar: true
    });
  }

  error(text = '') {
    this._toaster.error (this._translate.instant (text), '', {
      timeOut: 3000,
      closeButton: true,
      progressBar: true
    });
  }

  warning(text = '') {
    this._toaster.warning (this._translate.instant (text), '', {
      timeOut: 3000,
      closeButton: true,
      progressBar: true
    });
  }

  networkError(text = 'Currently unable to handle the request. HTTP Error 500') {
    this._toaster.error (this._translate.instant (text), '', {
      timeOut: 3000,
      closeButton: true,
      progressBar: true
    });
  }
}
