import { NotificationComponent } from './account/notification/notification.component';
import { AdminGuard } from './guard/admin.guard';
import { ManagerGuard } from './guard/manager.guard';
import { RoleGuard } from './guard/role.guard';
import { TempGuard } from './guard/temp.guard';
import { BlankComponent } from './layouts/blank/blank.component';
import { FullComponent } from './layouts/full/full.component';
import { AuthService } from './core/service/auth/auth.service';
import { LoginComponent } from './authentication/login/login.component';
import { PublicAuthGuard } from './guard/publicauth.guard';
import { LogoutComponent } from './authentication/logout/logout.component';
import { AuthGuard } from './guard/auth.guard';
import { environment } from 'src/environments/environment';
import { Routes } from '@angular/router';

let routes = [];
if (environment.production) {
  routes = [
    {
      path: '',
      component: FullComponent,
      children: [
        {
          path: '',
          redirectTo: '/login',
          pathMatch: 'full'
        },
        {
          path: 'notification',
          component: NotificationComponent,
          canActivate: [RoleGuard]
        },
        {
          path: 'notification/:id',
          component: NotificationComponent,
          canActivate: [RoleGuard]
        },
        {
          path: 'home',
          redirectTo: 'dashboard',
          pathMatch: 'full'
        },
        {
          path: 'employee-details',
          loadChildren: () => import ('./employee-details/employee-details.module').then (m => m.EmployeeDetailsModule),
           canActivate: [AdminGuard]
        },
        {
          path: 'dashboard',
          loadChildren: () => import ('./dashboard/dashboard.module').then (m => m.DashboardModule),
          canActivate: [AdminGuard]
        },
        {
          path: 'project-settings',
          loadChildren: () => import ('./project-settings/project-settings.module').then (m => m.ProjectSettingsModule),
          canActivate: [AdminGuard]
        },
        {
          path: 'task-entry',
          loadChildren: () => import ('./task-entry/task-entry.module').then (m => m.TaskEntryModule),
          canActivate: [RoleGuard]
        },
        {
          path: 'time-management',
          loadChildren: () => import ('./time-management/time-management.module').then (m => m.TimeManagementModule),
          canActivate: [ManagerGuard]
        },
        {
          path: 'control-panel',
          loadChildren: () => import ('./control-panel/control-pannel.module').then (m => m.ControlPanelModule),
          canActivate: [AdminGuard]
        },
      ]
    },
    {
      path: 'logout',
      redirectTo: 'account/logout',
      pathMatch: 'full'
    },
    {
      path: 'account/logout',
      component: LogoutComponent
    },
    {
      path: '',
      component: BlankComponent,
      children: [
        {
          path: 'login',
          component: LoginComponent,
          canActivate: [PublicAuthGuard]
        },
        {
          path: 'authentication',
          loadChildren:
            () => import ('./authentication/authentication.module').then (m => m.AuthenticationModule),
         canActivate: [PublicAuthGuard]
        },
      ]
    },
    {
      path: '**',
      redirectTo: '/login'
    }
  ];
} else {
  routes = [
    {
      path: '',
      component: FullComponent,
      children: [
        {
          path: '',
          redirectTo: '/login',
          pathMatch: 'full'
        },
        {
          path: 'notification/notification',
          redirectTo: 'account/logout',
              pathMatch: 'full'
        },
        {
          path: 'notification/:id',
          component: NotificationComponent,
          canActivate: [RoleGuard]
        },
        {
          path: 'home',
          redirectTo: 'dashboard',
          pathMatch: 'full'
        },
        {
          path: 'employee-details',
          loadChildren: () => import ('./employee-details/employee-details.module').then (m => m.EmployeeDetailsModule),
          canActivate: [AdminGuard]
        },
        {
          path: 'control-panel',
          loadChildren: () => import ('./control-panel/control-pannel.module').then (m => m.ControlPanelModule),
          canActivate: [AdminGuard]
        },
        {
          path: 'dashboard',
          loadChildren: () => import ('./dashboard/dashboard.module').then (m => m.DashboardModule),
          canActivate: [AdminGuard]
        },
        {
          path: 'project-settings',
          loadChildren: () => import ('./project-settings/project-settings.module').then (m => m.ProjectSettingsModule),
          canActivate: [AdminGuard]
        },
        {
          path: 'task-entry',
          loadChildren: () => import ('./task-entry/task-entry.module').then (m => m.TaskEntryModule),
          canActivate: [RoleGuard]
        },
        {
          path: 'time-management',
          loadChildren: () => import ('./time-management/time-management.module').then (m => m.TimeManagementModule),
          canActivate: [ManagerGuard]
        },
      ]
    },
    {
      path: 'logout',
      redirectTo: 'account/logout',
      pathMatch: 'full'
    },
    {
      path: 'account/logout',
      component: LogoutComponent
    },
    {
      path: '',
      component: BlankComponent,
      children: [
        {
          path: 'login',
          component: LoginComponent,
         canActivate: [PublicAuthGuard]
        },
        {
          path: 'authentication',
          loadChildren: () => import ('./authentication/authentication.module').then (m => m.AuthenticationModule),
          canActivate: [PublicAuthGuard]
        },
      ]
    },
    {
      path: '**',
      redirectTo: '/login'
    }
  ];
}
export const appRoutes: Routes = routes;
