
import {
  AfterViewChecked,
  AfterViewInit, ChangeDetectorRef, Component, ComponentFactoryResolver,
  EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output,
  SimpleChanges, ViewChild, ViewContainerRef
} from '@angular/core';

@Component ({
  selector: 'app-grid-item',
  templateUrl: './grid-item.component.html',
  styleUrls: ['./grid-item.component.scss']
})
export class GridItemComponent implements OnInit, AfterViewInit, OnChanges, OnDestroy , AfterViewChecked {
  @Input () valuePrepareFunction: (arg: any, row: any) => any;

  @Input () row: any;

  @Input () value: any;

  @Input () headValue;

  @Input () key;

  @Input () columnSetting: any = {};

  @ViewChild ('container', { read: ViewContainerRef }) private container: ViewContainerRef;

  @Output () customEvent = new EventEmitter ();

  private componentRef;

  constructor(private componentFactoryResolver: ComponentFactoryResolver, private _changeDetector: ChangeDetectorRef) { }

ngOnInit(): void {


  }

  ngOnChanges(): void {
    if (this.valuePrepareFunction) {
      this.value = this.valuePrepareFunction (this.value, this.row);
    }

  }

  ngAfterViewInit(): void {
    if (this.columnSetting.type !== 'custom') {
      return;
    }
    if (this.columnSetting.renderComponent) {
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory (this.columnSetting.renderComponent);
      const viewContainerRef = this.container;
      this.componentRef = this.container;
      viewContainerRef.clear ();
      const componentRef: any = viewContainerRef.createComponent (componentFactory);
      (componentRef.instance as any).value = this.value;
      (componentRef.instance as any).row = this.row;
      (componentRef.instance as any).headValue = this.headValue;
      (componentRef.instance as any).key = this.key;
      if ((componentRef.instance as any).customEvent) {
        (componentRef.instance as any).customEvent.subscribe (val => this.customEvent.emit (val)
        );

      }
    }
  }

  ngAfterViewChecked(): void {
    this._changeDetector.detectChanges ();
  }

  ngOnDestroy(): void {

  }
}

