import { Component, Input, OnInit } from '@angular/core';

@Component ({
  selector: 'app-badge',
  templateUrl: './badge.component.html',
  styleUrls: ['./badge.component.scss']
})
export class BadgeComponent implements OnInit {

  @Input () value: any;

  @Input () rowData: any;

  @Input () cell: any;

  @Input () color: any;

  @Input () fontColor = '#fff';

  constructor() { }

ngOnInit(): void {
    if (this.value) {
      this.color = this.value?.color;
      this.fontColor = this.value?.fontColor ? this.value?.fontColor : this.fontColor ;
      this.value = this.value?.value;
    }
  }

ngOnChanges() {

  }

}

