import { dateFormat, ApiStatus, userRole } from './../../core/constants/constants';
import { CommunicationService } from 'src/app/core/service/common/communication.service';
import { ToastService } from './../../core/helper/toast.helper.service';
import { Component, OnInit, EventEmitter, Output, OnDestroy, ViewChild, HostListener } from '@angular/core';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationsService } from 'src/app/core/service/notifications/notifications.service';
import { SubSink } from 'subsink';
import { Subject } from 'rxjs';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ValidationruleService } from 'src/app/core/helper/validationrule.service';
import { Title } from '@angular/platform-browser';

@Component ({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss']
})
export class NotificationComponent implements OnInit, OnDestroy {

  public tableSearch:string;

  public previousIndex = {
    main: null,
    mainSub: null,
    sub: null
  }

  public userRole = this._helper.getUser?.user_type

  public filters: Filter[] = [{name: 'clear'},{name: 'read'},{name: 'unflag'},{name: 'unread'}];

  public notification: notificationDetail;

  public notificationView = true;

  public unread = false;

  public offset = 1 ;

  public notificationList:  [Notification[],Notification[],Notification[]] ;

  public limit = 10;

  public notificationCount = 0;

  public sideSize = 'full';

  public sideView = false;

  public detailView = false;

  private id: string;

  private  idSelected = false ;

  private subs = new SubSink ();


  constructor(private _fun: FunctionService,
    private _notification: NotificationsService,
    private _toast: ToastService,
    private _helper: FunctionService,
    private _router: Router,
    private _activatedRoute: ActivatedRoute,
    private formBuilder: FormBuilder,
    private rule: ValidationruleService,
    private _communication: CommunicationService,
    private titleService: Title) { }

  ngOnInit(): void {
    if (this.userRole === userRole.employee) {
      this.detailView = true;
    }
    this.tableSearch = '';
    this.getFormId ();
    this.notificationGrid ();
    this.handleSidebar ();
  }

  toggleNotification() {
    this.sideView = !this.sideView;
  }

  @HostListener ('window:resize', ['$event'])
  onResize(event) {
    this.handleSidebar ();
  }

handleSidebar() {
    const innerWidth = window.innerWidth ;
    if (innerWidth > 767) {
      this.sideSize = 'full';
    }
     if (innerWidth < 767) {
      this.sideSize = 'small';
    }
  }

getFormId () {
  this.subs.add (
    this._activatedRoute.params.subscribe (res => {
      this.id = res['id'];
      if (this.id !== 'notification') {
        this.notificationDetails (this.id);
      } else {
        this.notificationView = true;
      }
    })
  );
}

notificationGrid( offSet = 1, search = this.tableSearch, limit = this.limit) {
    const gridParams = {
      q: search,
      offset: (offSet - 1) * this.limit ,
      limit: limit,
    };
    this.subs.add (
      this._notification.getViewNotification ( gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.notificationList = [[],[],[]];
          this.notificationPush (res.emails);
          this.notificationCount = res?.count;
        }
      })
    );
  }

  notificationPush (notification) {
    const currentDate = new Date ().setHours (0,0,0,0);
    const week = new Date ();
    week.setDate (week.getDate () - 7);
    const thisWeek = week.setHours (0,0,0,0);

    notification.map ((value) => {
      const group = new Date (value.group).setHours (0,0,0,0);
      if ( group === currentDate ) {
      this.notificationList[0].push (value);
      this.checkSelection (value , 0,this.notificationList[0]?.length);
      }
      if (thisWeek <=  group && group !== currentDate) {
        this.notificationList[1].push (value);
        this.checkSelection (value , 1,this.notificationList[1]?.length);
        }
        if (thisWeek >  group ) {
          this.notificationList[2].push (value);
          this.checkSelection (value , 2,this.notificationList[2]?.length);
          }
    });
    if (this.idSelected) {
      this.notificationList[this.previousIndex.main][this.previousIndex.mainSub].list[this.previousIndex.sub]['clicked'] = true;
    }

    }

    checkSelection(value ,i,j) {
      if (this.id ) {
          const k =  value.list.findIndex ( (f)=> {
          return f.notification_id === parseInt (this.id);
     });
    if ( k !== -1){
      this.previousIndex = {
        main: i,
        mainSub: j - 1,
        sub: k
      };
      this.idSelected = true;
    }
}
}

notificationScroll( offSet = 1, search = this.tableSearch, limit = this.limit) {
  const gridParams = {
    q: search,
    offset: (offSet - 1) * this.limit,
    limit: limit,
  };
  this.subs.add (
    this._notification.getViewNotification ( gridParams).subscribe (res => {
      if (ApiStatus.ok === res.status) {
        this.notificationCount = res?.count;
        this.notificationPush (res.emails) ;
      }
    })
  );
}

  notificationClick(ev,i,j,k) {
    if (this.previousIndex.main !== null && this.previousIndex.sub  !== null &&  this.previousIndex.mainSub  !== null ) {
     this.notificationList[this.previousIndex.main][this.previousIndex.mainSub].list[this.previousIndex.sub]['clicked'] = false;
    }
    this.previousIndex = {
      main: i,
      mainSub: j,
      sub: k
    };
    ev['clicked'] = true;
    this.notificationDetails (ev.notification_id);
  }

  notificationDetails (id) {
    this._router.navigate ([`/notification/${id}`]);
    this.subs.add (
      this._notification.getNotificationDetails ( id).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.sideView = false;
          this.notificationView = false;
          this.notification = res;
        }
      })
    );
  }

  filterEvent(filter) {

  }

  search() {
    this.notificationGrid (1);
    this.offset = 1;
  }

  onScroll() {
     if (this.notificationCount > (this.offset * this.limit)) {
      this.offset = this.offset + 1;
      this.notificationScroll ( this.offset, this.tableSearch,this.limit );
     }

  }

  viewDetailsClick(id) {
    if (this.detailView) {
      this._router.navigate ([`/task-entry/details/${id}`]);
    } else {
      this._router.navigate ([`/time-management/detail/${id}`]);
    }
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe ();
  }
}

interface Filter {
  name: string;
}

interface Notification {
  group:string,
  list:NotificationList[]
}
interface NotificationList   {
  timeslice: string,
  timestamp:  string,
  notification_id: number,
  recipient: {
      name: string
  },
  actor: {
      name: string
  },
  action_object_content_type: string,
  unread: boolean,
  verb: string,
  description: string,
  type:string,
  data: string,
  id: number
}

interface notificationDetail {
  type: string;
  object: string;
  user_type: string;
  notification: string;
  id: number;
  data?: any;
  user: string;
  time_object: number;
  object_id: string;
  content: string;
  timeslice: string;
  notification_data: Notificationdatum[];
  format: string;
}

interface Notificationdatum {
  key: string;
  value: string;
}
