import { ShortTextPipe } from 'src/app/core/pipe/short-text.pipe';
import { Component, ElementRef, EventEmitter, Input, OnChanges, OnInit, Output, ViewChild, OnDestroy } from '@angular/core';
import * as d3 from 'd3';
import { Arc, Pie, Selection } from 'd3';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { D3DonutGraphFormattedData, D3DonutGraphDataFromParent, D3DonutChartConfiguration } from 'src/app/core/interface/common.interface';
import { ShortNumberPipe } from 'src/app/core/pipe/short-number.pipe';

@Component ({
  selector: 'app-donut-graph',
  templateUrl: './donut-graph.component.html',
  styleUrls: ['./donut-graph.component.scss']
})
export class DonutGraphComponent implements OnInit, OnChanges, OnDestroy {
  @ViewChild ('donutChart', { static: true }) private chartContainer: ElementRef;

  @ViewChild ('donutChartTooltip', { static: true }) private tooltipContainer: ElementRef;

  @Output () pointSelection = new EventEmitter ();

  @Input () private data: D3DonutGraphDataFromParent[];

  @Input () private clickable = true;

  @Input () private center = '-';

  @Input () private time = '-';

  @Input () private subCenter: string;

  @Input () private centerSymbol: string;

  @Input () private chartConfiguration: D3DonutChartConfiguration = {
    width: 220,
    height: 220,
    margin: { top: 25, right: 25, bottom: 25, left: 25 },
    innerRadius: 40,
    outerRadius: 0,
    centerTextFontSize: '30px',
    dataLabelFontSize: '16px',
    centerTextFontWeight: 600,
    subCenterTextFontSize: '11px',
  };

  private radius: number;

  private innerRadius: number;

  private outerRadius: number;

  private pie: Pie<any, any> | any;

  private arc: Arc<any, any>;

  private svg: Selection<SVGSVGElement | any, any, any, any>;

  private path: Selection<SVGPathElement | any, any, any, any>;

  private text: Selection<SVGPathElement | any, any, any, any>;

  private enterClockwise = {
    startAngle: 0,
    endAngle: 0
  };

  private enterAntiClockwise = {
    startAngle: Math.PI * 2,
    endAngle: Math.PI * 2
  };

  private placeholderDataset = [
    {
      label: 'No Data', data: 1, color: '#e9e9e9'
    }
  ];

  private timeOut; // Variable to clear timeout

constructor(
    private _helper: FunctionService,
    private _shortNumber: ShortNumberPipe,
    private _shortText: ShortTextPipe,
  ) { }

ngOnInit(): void {
    this.drawChart (this.placeholderDataset);
    this.setCenterText ();
  }

ngOnChanges(): void {
    this.setRadius ();
    this.timeOut = setTimeout (() => {
      if (this.svg) {
        if (this.data?.length !== 0) {
          this.updateChart (this.data);
          this.setCenterText ();
        } else {
          this.center = '0';
          this.updateChart (this.placeholderDataset);
          this.setCenterText ();
        }
      }
    }, 100);
  }

/**
  * @description Function used to draw the placeholder chart
  * @author Sreejith T
  * @date 28/09/2021
  * @param {D3DonutGraphDataFromParent[]} data
  */
  drawChart(data: D3DonutGraphDataFromParent[]): void {
    this.pie = d3.pie ()
      .padAngle (0.005)
      .value ((d: D3DonutGraphDataFromParent | any) => d.data)
      .sort (null);

    this.arc = d3.arc ()
      .innerRadius (this.innerRadius)
      .outerRadius (this.outerRadius);

    this.svg = d3.select (this.chartContainer.nativeElement)
      .append ('svg')
      .attr ('viewBox', `${-(this.chartConfiguration.width + this.chartConfiguration.margin.left + this.chartConfiguration.margin.right) / 2} ${-(this.chartConfiguration.height + this.chartConfiguration.margin.top + this.chartConfiguration.margin.bottom) / 2} ${this.chartConfiguration.width + this.chartConfiguration.margin.left + this.chartConfiguration.margin.right} ${this.chartConfiguration.height + this.chartConfiguration.margin.top + this.chartConfiguration.margin.bottom}`)
      .attr ('preserveAspectRatio', 'xMinYMin meet')
      .attr ('class','w-100');

    this.path = this.svg.selectAll ('path')
      .data (this.pie (data), (d: D3DonutGraphFormattedData) => d.data.label)
      .enter ()
      .append ('path')
      .attr ('class', 'slice')
      .attr ('fill', (d: D3DonutGraphFormattedData) => d.data.color)
      .attr ('d', this.arc (this.enterAntiClockwise))
      .each ((d: D3DonutGraphFormattedData, index: number, n: any) => {
        n[index]._current = d;
      });

    this.path.transition ()
      .duration (1000)
      .attrTween ('d', (d: D3DonutGraphFormattedData, index: number, n: any) => {
        const i = d3.interpolate (n[index]._current, d);
        n[index]._current = i (0);
        return t => this.arc (i (t));
      });
  }

/**
   * @description Function used to update the data labels and their position
   * @author Sreejith T
   * @date 28/09/2021
   * @param {D3DonutGraphDataFromParent[]} data
   */
  updateLabel(data: D3DonutGraphDataFromParent[]): void {
    this.text = this.svg.selectAll ('.dataLabels')
      .data (this.pie (data), (d: D3DonutGraphFormattedData) => d.data.label);

    this.text
      .enter ()
      .append ('text')
      .on ('mouseover', (event: MouseEvent, data: D3DonutGraphFormattedData | any) => this.onMouseOver (event, data))
      .on ('mousemove', (event: MouseEvent) => this.onMouseMove (event))
      .on ('mouseout', (event: MouseEvent) => this.onMouseOut (event))
      .attr ('class', 'dataLabels')
      .attr ('text-anchor', 'middle')
      .attr ('data-name', (d: D3DonutGraphFormattedData) => d.data.label)
      .style ('font-size', this.chartConfiguration.dataLabelFontSize)
      .style ('font-family', 'Verdana, Geneva, sans-serif')
      .attr ('fill', (d: D3DonutGraphFormattedData) => this._helper.invertColor (d.data.color))
      .attr ('cursor', 'pointer')
      .style ('font-weight', 400)
      .attr ('dy', '.35em')
      .transition ().duration (1000)
      .attrTween ('transform', (d: D3DonutGraphFormattedData, index: number, n: any) => {
        n[index]._current = {
          data: d.data,
          value: d.value,
          index: d.index,
          padAngle: d.padAngle,
          startAngle: this.enterClockwise.startAngle,
          endAngle: this.enterClockwise.endAngle
        };
        const i = d3.interpolate (n[index]._current, d);
        n[index]._current = i (0);
        return t => {
          const d2 = i (t);
          const pos = this.arc.centroid (d2);
          return 'translate(' + pos + ')';
        };
      })
      .text ((d: D3DonutGraphFormattedData) => {
        if (this.getPercentage (d.data.data, this.center) > 3) {
          return this.getPercentage (d.data.data, this.center) + '%';
        }
      });

    this.text.exit ().remove ();

    this.text
      .transition ().duration (1000)
      .attrTween ('transform', (d: D3DonutGraphFormattedData, index: number, n: any) => {
        const i = d3.interpolate (n[index]._current, d);
        n[index]._current = i (0);
        return t => {
          const d2 = i (t);
          const pos = this.arc.centroid (d2);
          return 'translate(' + pos + ')';
        };
      })
      .text ((d: D3DonutGraphFormattedData) => {
        if (this.getPercentage (d.data.data, this.center) > 3) {
          return this.getPercentage (d.data.data, this.center) + '%';
        }
      });

  }

/**
   * @description Function used to update the chart according to the data
   * @author Sreejith T
   * @date 28/09/2021
   * @param {D3DonutGraphDataFromParent[]} data
   */
  updateChart(data: D3DonutGraphDataFromParent[]): void {

    data = data?.filter ((d: D3DonutGraphDataFromParent) => {
      if (d.data !== 0) {
        return d;
      }
    });

    this.path = this.svg.selectAll ('path').data (this.pie (data), (d: D3DonutGraphFormattedData) => d.data.label);

    this.path
      .enter ().append ('path')
      .on ('click', (event: MouseEvent, data: D3DonutGraphFormattedData | any) => this.onClick (event, data))
      .on ('mouseover', (event: MouseEvent, data: D3DonutGraphFormattedData | any) => this.onMouseOver (event, data))
      .on ('mousemove', (event: MouseEvent) => this.onMouseMove (event))
      .on ('mouseout', (event: MouseEvent) => this.onMouseOut (event))
      .attr ('class', 'slice')
      .attr ('fill', (d: D3DonutGraphFormattedData) => d.data.color)
      .attr ('d', this.arc (this.enterClockwise))
      .each ((d: D3DonutGraphFormattedData, index: number, n: any) => {
        n[index]._current = {
          data: d.data,
          value: d.value,
          index: d.index,
          padAngle: d.padAngle,
          startAngle: this.enterClockwise.startAngle,
          endAngle: this.enterClockwise.endAngle
        };
      })
      .transition ()
      .duration (1000)
      .attrTween ('d', (d: D3DonutGraphFormattedData, index: number, n: any) => {
        const i = d3.interpolate (n[index]._current, d);
        n[index]._current = i (0);
        return t => this.arc (i (t));
      });

    this.path.exit ()
      .transition ()
      .duration (1000)
      .attrTween ('d', (d: D3DonutGraphFormattedData, index: number, n: any) => {
        const i = d3.interpolate (n[index]._current,
          {
            startAngle: (d.data.label === 'No Data' ? this.enterAntiClockwise.startAngle : this.enterClockwise.startAngle),
            endAngle: (d.data.label === 'No Data' ? this.enterAntiClockwise.endAngle : this.enterClockwise.endAngle), value: 0
          });
        n[index]._current = i (0);
        return t => this.arc (i (t));
      })
      .remove ();

    this.path.transition ()
      .duration (1000)
      .attr ('class', 'slice')
      .attrTween ('d', (d: D3DonutGraphFormattedData, index: number, n: any) => {
        const i = d3.interpolate (n[index]._current, d);
        n[index]._current = i (0);
        return t => this.arc (i (t));
      });

    this.updateLabel (data);
  }

/**
 * @description Function used for the emitting the data required for filter and is triggered on clicking the path
 * @author Sreejith T
 * @date 28/09/2021
 * @param {MouseEvent} event
 * @param {D3DonutGraphFormattedData)} data
 */
  onClick(event: MouseEvent | any, data: D3DonutGraphFormattedData): void {
    if (this.clickable) {
      const name = 'clicked';
      const elem = d3.select (event.currentTarget);
      const className = elem.attr ('class');
      const dataForEmit = {
        label: data.data.label,
        value: data.data.data,
        identifier: data.data.identifier
      };
      this.animatePath (elem, className.split (' ')[0] === name ? 1 : 0, name);
      this.pointSelection.emit (dataForEmit);
    }
  }

/**
  * @description Function used for animating the clicked path (increase/decrease the outer radius)
  * @author Sreejith T
  * @date 28/09/2021
  * @param {Selection<SVGSVGElement | any, any, any, any>} path
  * @param {number} dir
  * @param {string} className
  */
  animatePath(path: Selection<SVGSVGElement | any, any, any, any>, dir: number, className: string): void {
    switch (dir) {
      case 0:
        d3.selectAll ('.' + className)
          .attr ('d', d3.arc ()
            .innerRadius ((this.innerRadius))
            .outerRadius (this.outerRadius)
          );
        path.transition ()
          .duration (500)
          .attr ('class', className + ' slice clicked')
          .ease (d3.easeBounce)
          .attr ('d', d3.arc ()
            .innerRadius ((this.innerRadius))
            .outerRadius ((this.outerRadius) * 1.08)
          );
        break;
      case 1:
        path.transition ()
          .attr ('class', 'slice')
          .transition ()
          .duration (500)
          .ease (d3.easeBounce)
          .attr ('d', d3.arc ()
            .innerRadius ((this.innerRadius))
            .outerRadius (this.outerRadius)
          );
        break;
    }
  }

/**
   * @description Function used to set the center text
   * @author Sreejith T
   * @date 28/09/2021
   */
  setCenterText(): void {
    this.svg.selectAll ('.d3-donut-center').remove ();
    if (this.subCenter) {
      const textCenter = this.svg
      .append ('text')
      .attr ('transform', 'translate(-25, -10)')
      .attr ('text-anchor', 'middle')
      .attr ('class', 'd3-donut-center')
      .style ('font-size', this.chartConfiguration.centerTextFontSize)
      .style ('font-family', 'Verdana, Geneva, sans-serif')
      .style ('fill', '#555')
      .style ('font-weight', this.chartConfiguration.centerTextFontWeight)
      // tslint:disable-next-line:radix
      .text (this._shortNumber.transform (this.time));
      this.svg
      .append ('text')
      .attr ('transform', 'translate(30 , -10)')
      .attr ('text-anchor', 'middle')
      .attr ('class', 'd3-donut-center')
      .style ('font-size', '18px')
      .style ('font-family', 'Verdana, Geneva, sans-serif')
      .style ('fill', '#555')
      .style ('font-weight', this.chartConfiguration.centerTextFontWeight)
      .text (this.centerSymbol);

      const textSubText = this.svg
      .append ('text')
      .attr ('transform', 'translate(0 , 25)')
      .attr ('text-anchor', 'middle')
      .attr ('class', 'd3-donut-center')
      .style ('font-size', this.chartConfiguration.subCenterTextFontSize)
      .style ('font-family', 'Verdana, Geneva, sans-serif')
      .style ('fill', '#555')
      .style ('font-weight', this.chartConfiguration.centerTextFontWeight)
      .text (this._shortText.transform (this.subCenter,18));
    } else {
      const textCenter = this.svg
      .append ('text')
      .attr ('transform', 'translate(0 , 10)')
      .attr ('text-anchor', 'middle')
      .attr ('class', 'd3-donut-center')
      .style ('font-size', this.chartConfiguration.centerTextFontSize)
      .style ('font-family', 'Verdana, Geneva, sans-serif')
      .style ('fill', '#555')
      .style ('font-weight', this.chartConfiguration.centerTextFontWeight)
      // tslint:disable-next-line:radix
      .text (this._shortNumber.transform (parseInt (this.center)));
    }


  }

/**
   * @description Function used to set the radius of the chart according to the configuration sent from parent component
   * @author Sreejith T
   * @date 28/09/2021
   */
  setRadius(): void {
    this.radius = Math.min (this.chartConfiguration.width, this.chartConfiguration.height) / 2;
    this.innerRadius = this.radius - this.chartConfiguration.innerRadius;
    this.outerRadius = this.radius - this.chartConfiguration.outerRadius;
  }

/**
  * @description Function used to calculate the percentage of the value with the total
  * @author Sreejith T
  * @date 28/09/2021
  * @param {value} number
  * @param {string | number | any} total
  */
  getPercentage(value: number, total: string | number | any): number {
    try {
      const percentage: string | number | any = Math.round ((value / total) * 100);
      // tslint:disable-next-line:radix
      return parseInt (percentage);
    } catch (error) {
      return 0;
    }
  }

/**
   * @description Function used to show the tooltip when is mouse is over on top of the path
   * @author Sreejith T
   * @date 28/09/2021
   * @param {MouseEvent} event
   * @param {D3DonutGraphFormattedData} data
   */
  onMouseOver(event: MouseEvent, data: D3DonutGraphFormattedData): void {
    if (data?.data?.label === 'No Data') {
      return;
    }
    d3.select (this.tooltipContainer.nativeElement)
      .html (`<div>
    <label class="tool-tip-text w-100 text-center">${data.data.label}</label>
    <div class="d-flex align-items-center justify-content-center">
    <span class="tool-tip-marker" style="background-color:${data.data.color}"></span>
    <label class="tool-tip-text">${data.data.data}</label></div>
    </div>`)
      .style ('display', 'flex');
  }

/**
   * @description Function used to move the tooltip according the position of the mouse pointer
   * @author Sreejith T
   * @date 28/09/2021
   * @param {MouseEvent} event
   */
  onMouseMove(event: MouseEvent): void {
    d3.select (this.tooltipContainer.nativeElement)
      .style ('top', event.offsetY - 10 + 'px')
      .style ('left', event.offsetX + 20 + 'px');
  }

/**
   * @description Function used to hide the tooltip when is mouse is moved out from the path
   * @author Sreejith T
   * @date 28/09/2021
   * @param {MouseEvent} event
   */
  onMouseOut(event: MouseEvent): void {
    d3.select (this.tooltipContainer.nativeElement)
      .html ('')
      .style ('display', 'none');
  }

ngOnDestroy(): void {
    clearTimeout (this.timeOut);
  }
}
