import { dateFormat } from '../constants/constants';
import { environment } from 'src/environments/environment';
import { LocalStorageKey } from 'src/app/core/constants/constants';
import { StorageService } from 'src/app/core/helper/storage.service';
import { Injectable, EventEmitter } from '@angular/core';

import { DatePipe } from '@angular/common';
import * as moment from 'moment';
import { CookieService } from 'ngx-cookie-service';
import { LoginObject } from '../service/login/login.service';

@Injectable ({
  providedIn: 'root'
})
export class FunctionService {
  sidebarSubMenuItem: EventEmitter<string> = new EventEmitter ();

  sidebarSettingsSubMenuItem: EventEmitter<string> = new EventEmitter ();

  public userProfile: any;

  public logoImage: any;

  public randomIntegerValue = 0;

public layoutOptions = {
    theme: 'light', // two possible values: light, dark
    dir: 'ltr', // two possible values: ltr, rtl
    layout: 'vertical', // fixed value. shouldn't be changed.
    sideBarType: 'full', // four possible values: full, iconbar, overlay, mini-sidebar
    sideBarPosition: 'fixed', // two possible values: fixed, absolute
    headerPosition: 'fixed', // two possible values: fixed, absolute
    boxed: 'full', // two possible values: full, boxed
    navBarBg: 'skin6', // six possible values: skin(1/2/3/4/5/6)
    sideBarBg: 'skin5', // six possible values: skin(1/2/3/4/5/6)
    logoBg: 'skin6' // six possible values: skin(1/2/3/4/5/6)
  };

  constructor(private datePipe: DatePipe,
    private _cookie: CookieService,
    private _storage: StorageService) { }

get random() {
    return Math.floor (Math.random () * 6) + 1;
  }

getRandomString(length = 5) {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for (let i = 0; i < length; i++) {
      result += characters.charAt (Math.floor (Math.random () * charactersLength));
    }
    return result;
  }

getPercentage(value: any, total: any): any {
    const radix = 10 ;
    try {
      const percentage: any = (value / total) * 100;
      // eslint-disable-next-line radix
      if (isNaN (parseInt (percentage, radix))) {
        return 0;
      } else {
        return parseInt (percentage, radix);
      }
    } catch (error) {
      return 0;
    }
  }

public convertToServerDate(date: any = new Date (), format: string = dateFormat.serverDate) {
    try {
      return moment (date).format (format);
    } catch (error) {
      return date;
    }
  }

public convertToCreateTimeApiFormat(date: any = new Date (), format: string = dateFormat.createTimeApiFormat) {
    try {

      // return moment(date).format('YYYY-DD-MM');
      return this.datePipe.transform (date, format);
    } catch (error) {
      return date;
    }
  }

public convertToCreateTimeApiTimeFormat(date: any = new Date (), format: string = dateFormat.addTime) {
    try {

      // return moment(date).format('YYYY-DD-MM');
      return this.datePipe.transform (date, format);
    } catch (error) {
      return date;
    }
  }

public convertToClientDate(date: any = new Date (), format: string = dateFormat.userDate) {
    try {
      return this.datePipe.transform (date, format);
    } catch (error) {
      return date;
    }
  }

public convertDateToTimesheetFormat(date: any, format = dateFormat.timeSheetDate) {
    try {
      return moment (date).format (format); // this.datePipe.transform(date, format);
    } catch (error) {
      return date;
    }
  }

public getCurrentWeek(date = moment (), format = dateFormat.serverDate) {
    const currentDate = moment (date);
    const weekStart = currentDate.clone ().startOf ('week');
    const days = [];
    for (let i = 0; i <= 6; i++) {
      days.push (moment (weekStart).add (i, 'days').format (format));
    }
    return days;
  }

public getPreviousWeek(date = moment (), format = dateFormat.timeSheetDate) {
    const currentDate = moment (date).subtract (1, 'days');
    const weekStart = currentDate.clone ().startOf ('week');
    const days = [];
    for (let i = 0; i <= 6; i++) {
      days.push (moment (weekStart).add (i, 'days').format (format));
    }
    return days;
  }

public getNextWeek(date = moment (), format = dateFormat.timeSheetDate) {
    const currentDate = moment (date).add (1, 'days');
    const weekStart = currentDate.clone ().startOf ('week');
    const days = [];
    for (let i = 0; i <= 6; i++) {
      days.push (moment (weekStart).add (i, 'days').format (format));
    }
    return days;
  }

public getDateRange(type: any = 'days', range = 12, format = dateFormat.timeSheetDateFilter, date = moment ()) {
    const t: any = type;
    const currentDate = moment (date).subtract (range, t);
    const weekStart = currentDate.clone ().startOf ('day');
    const days = [];

    for (let i = 0; i <= range; i++) {
      days.push (moment (weekStart).add (i, t).format (format));
    }
    return days;
  }

public stringToDate(date = moment (), format = dateFormat.date) {
    try {
      return moment (date).format (format);
    } catch (error) {
      return date;
    }
  }

public stringToTime(time: string, date: any = moment (), format = dateFormat.fullDate) {
    try {
      return moment (`${date} ${time}`).format (format);
    } catch (error) {
      return time;
    }
  }

public getMovementDate(date: any, format = dateFormat.movementTime) {
    try {
      return moment (date).format (format); // this.datePipe.transform(date, format);
    } catch (error) {
      return date;
    }
  }

public getNextYearDate(date = moment (), format = dateFormat.fullDate) {
    return moment (date).add (1, 'years').format (format);
  }

public getPreviousYearDate(date = moment (), format = dateFormat.fullDate) {
    return new Date (moment (date).subtract (1, 'years').format (format));
  }

public convertStringToJson(data: any) {
    for (let i = 0; i < data.length; i++) {
      data = data.replace ('\'', '"');
    }
    return JSON.parse (data);
  }

public getUserDetails(): UserProfile {
    return this._storage.getLocalStorage (LocalStorageKey.userDetails);
  }

public setUser(data: LoginObject) {
    // Checking production variable for setting values
    if (environment.production) {
      document.cookie = `${LocalStorageKey.userLogin}=${JSON.stringify (data)};path=/;Domain=.${environment.cookieDomain}`;
    } else {
      document.cookie = `${LocalStorageKey.userLogin}=${JSON.stringify (data)};path=/;`;
    }
  }

public get getUser(): any {
    if (this._cookie.get (LocalStorageKey.userLogin)) {
      return  JSON.parse (this._cookie.get (LocalStorageKey.userLogin));
    }
    return '';
  }

public get isLogged(): any {
    if (environment.production) {
      if (this._cookie.get (LocalStorageKey.sessionId)) {
        return this._cookie.get (LocalStorageKey.sessionId);
      }
    } else {
      return this.getUser;
    }
    return true;
  }

public isValidEmail(email: string) {
    if (/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test (email)) {
      return (true);
    }
    return (false);
  }

public deleteCookie() {
    return new Promise ((resolve, reject) => {
      const cookies = document.cookie.split ('; ');
      for (let c = 0; c < cookies.length; c++) {
        const d = window.location.hostname.split ('.');
        while (d.length > 0) {
          const cookieBase = encodeURIComponent (cookies[c].split (';')[0].split ('=')[0]) + '=; expires=Thu, 01-Jan-1970 00:00:01 GMT; domain=' + d.join ('.') + ' ;path=';
          const p = location.pathname.split ('/');
          document.cookie = cookieBase + '/';
          while (p.length > 0) {
            document.cookie = cookieBase + p.join ('/');
            p.pop ();
          }
          d.shift ();
        }
      }
      document.cookie.split ('; ').map (v => {
        document.cookie = v.split ('=')[0] + `=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;Domain=.${environment.cookieDomain}`;
      });
      document.cookie = LocalStorageKey.userLogin + '=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
      // document.cookie = 'key' + `=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;`;
      document.cookie = 'sso-sessionid' + '=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
      document.cookie = 'auth' + '=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
      document.cookie = 'csrftoken' + '=; Path=/; Expires=Thu, 01 Jan 1970 00:00:01 GMT;';
      return resolve (true);
    });
  }

  invertColor(hex: any) {
    try {
      if (hex.indexOf ('#') === 0) {
        hex = hex.slice (1);
      }
      // convert 3-digit hex to 6-digits.
      if (hex.length === 3) {
        hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
      }
      if (hex.length !== 6) {
        return '#000';
      }
      const rgb = this.hexToRgb (hex);
      if (rgb.r > 190) {
        return '#000';
      }
      return '#fff';
    } catch (error) {
      return '#fff';
    }
  }

  hexToRgb(hex: any): any{
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec (hex);
    return result ? {
      r: parseInt (result[1], 16),
      g: parseInt (result[2], 16),
      b: parseInt (result[3], 16)
    } : null;
  }

  timeConverter (time) {
    if (time) {
    if (time?.includes ('AM' || 'am' || 'Am')) {
    const timeArray = time.split (':', 2);
    if (timeArray.length === 2 ) {
      return  (timeArray[0] === '12' ? '00' :  timeArray[0] ) + ':' + timeArray[1].slice (0, 2);
    } else {
      return  false;
    }
    }
    if (time?.includes ('PM' || 'Pm' || 'pm')) {
      const timeArray = time.split (':', 2);
      if (timeArray.length === 2 ) {
        return  (+ timeArray[0] + 12) + ':' + (timeArray[1].slice (0, 2));
      } else {
        return  false;
      }
      }
    } else {
      return time;
    }
    return time;
  }
}


export interface UserProfile {
  id: number;
  first_name: string;
  last_name: string;
  designation: string;
  contact_number: string;
  team: string;
  reporting_manager: string;
  joining_date: string;
  role: string;
  alternative_contact_number: string;
  avatar: string;
  linkdin_id: string;
  biography?: any;
  username: string;
  email: string;
}
