import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable ({
  providedIn: 'root'
})
export class LoaderService {
  isLoading = new BehaviorSubject<boolean> (false);

constructor() { }

  hide(): void {
    this.isLoading.next (false);
  }

  show(): void {
    this.isLoading.next (true);

  }
}
