import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { environment } from 'src/environments/environment';


@Injectable ({
  providedIn: 'root'
})
export class HttpService {
  apiUrl = environment.baseUrl + environment.baseVersion + environment.apiVersion;

constructor(
    private http: HttpClient) { }

post(url: string, data = {}): Observable<any | any[]> {
    return this.http.post (this.apiUrl + url, data);
  }

  fileUpload(url: string, data = {}): Observable<any | any[]> {
    return this.http.post (this.apiUrl + url, data);
  }

  put(url: string, data = {}): Observable<any | any[]> {
    return this.http.put (this.apiUrl + url, data);
  }

  get(url: string): Observable<any | any[]> {
    return this.http.get (this.apiUrl + url, );
  }

  delete(url: string): Observable<any | any[]> {
    return this.http.delete (this.apiUrl + url);
  }

}
