import { loginUrls } from './../../core/constants/login-constants';
import { userRole } from 'src/app/core/constants/constants';
import { ApiStatus } from './../../core/constants/constants';


import { FunctionService, UserProfile } from 'src/app/core/helper/function.helper.service';
import { Component,  OnInit, EventEmitter, Output, OnDestroy, Inject, ViewChild, ElementRef, Input } from '@angular/core';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { Router, NavigationEnd } from '@angular/router';
import { LoginService } from 'src/app/core/service/login/login.service';
import { StorageService } from 'src/app/core/helper/storage.service';
import { CommonApiService } from 'src/app/core/service/common/common-api.service';
import { ToastService } from 'src/app/core/helper/toast.helper.service';
import { TranslateService } from '@ngx-translate/core';
import { FormControl } from '@angular/forms';
import { SubSink } from 'subsink';
@Component ({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit, OnDestroy {

  @Input () sideBarType: string;

  @Output () toggleSidebar = new EventEmitter<void> ();

  @Output () closeSideBar = new EventEmitter<void> ();

  @Output () selectedFilters = new EventEmitter<void> ();

  public config: PerfectScrollbarConfigInterface = {};

  public showSearch = false;

  public showSettings = false;

  public filterHeader = [];

  public languageDropdown = [];

  public languageSelected;

  public notificationLimit = 10;

  public showLanguages = false;

  public notificationCount = 0;

  public userProfile: UserProfile;

  // This is for Notifications
  public notification = [];

  public selectedFilter = [];

  public isAllChecked = true;

  public unread = false;

  public firstSix = [];

  private subs = new SubSink ();

  public redirectUri

  public offsetNotification = 1;

  public languageselector = [];

  public askauraaInput = new FormControl ();

  public filteraskAuraa;

  public showNotificationList: boolean;

  public userRoles = this._function?.getUser?.user_type;

  public previousStrLength = 0;

  showSearchButton = false;

constructor(
    private _commonApi: CommonApiService,
    private _router: Router,
    private login: LoginService,
    public _function: FunctionService,
    private _storage: StorageService,
    private translate: TranslateService,
    private _toast: ToastService) {
     _router.events.subscribe ((val) => {
      if (val instanceof NavigationEnd) {
        this.notificationsCount ();
      }
     });
   }

ngOnInit(): void {
this.notificationsCount ();
  }

  toggle() {
    this.toggleSidebar.emit ();
  }

  logout() {
    this._router.navigate (['/logout']);
  }


  onScroll() {
    if (this.notificationCount > (this.offsetNotification * this.notificationLimit)) {
     this.offsetNotification = this.offsetNotification + 1;
     this.notificationScroll ( this.offsetNotification,this.notificationLimit );
    }

 }

 notificationScroll( offSet = 1, limit = this.notificationLimit) {
  const gridParams = {
    q: '',
    offset: (offSet - 1) * this.notificationLimit,
    limit: limit,
  };
  this.subs.add (
    this._commonApi.notification ( gridParams).subscribe (res => {
      if (ApiStatus.ok === res.status) {
        this.notification.push ( ...res?.results) ;
      }
    })
  );
}


  notificationList(offSet = 1 ,limit = this.notificationLimit) {
    this.notification = [];
    const gridParams = {
      q: '',
      offset: (offSet - 1) * this.notificationLimit ,
      limit: limit,
    };
    this.subs.add (
      this._commonApi.notification (gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.notification = res.results;
      }
}
    ));
  }

  notificationsCount () {
    this.subs.add (
      this._commonApi.notificationCount ().subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.notificationCount = res?.count;
      }
}
    ));
  }

  onNotificationIdClick(item) {
    this.closeNotification ();
    this._router.navigate ([`/notification/${item?.notification_id}`]);
  }

  openNotification () {
    this.offsetNotification = 1;
    this.notificationList ();
    this.showNotificationList = true;
  }

  closeNotification () {
    this.showNotificationList = false;
  }

  viewAllNotification() {
    this.closeSideBar.emit ();
    this.closeNotification ();
    this._router.navigate (['/notification/notification']);
  }

  settings (type) {
    if (type === 'setting') {
      if ( this.userRoles  === userRole.admin) {
        this._router.navigate ([ loginUrls.dashboard]);
      } else {
        this._router.navigate ([ loginUrls.employee]);
      }
    }
    if (type === 'home') {
      this._router.navigate (['/control-panel/user']);
    }
  }

  ngOnDestroy() {

    this.subs.unsubscribe ();
  }

}


