import { Injectable } from '@angular/core';
import { FunctionService } from './../../helper/function.helper.service';

@Injectable ({
  providedIn: 'root'
})
export class AuthService {

  constructor(
    private _helper: FunctionService, ) { }

public get isAuth(): string {
    if (this._helper.getUser) {
      return this._helper.getUser;
    }
    return '';
  }

public get isLoggedIn(): boolean {
    if (this._helper.isLogged) {
      return true;
    }
    return false;
  }

}
