import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { NgbDropdownConfig } from '@ng-bootstrap/ng-bootstrap';

@Component ({
  selector: 'app-tablemoreoption',
  templateUrl: './tablemoreoption.component.html',
  styleUrls: ['./tablemoreoption.component.scss']
})
export class TableMoreOptionComponent  {

  @Output () optionClick = new EventEmitter ();

  @Input () row: any;

  @Input () value: any;

  @Output () customEvent = new EventEmitter ();

constructor(config: NgbDropdownConfig) {
    config.placement = 'auto';
    config.autoClose = true;
  }

  eventFired(ev: string) {
    const data = {
      data: this.row,
      event: ev
    };
    this.customEvent.emit (data);
    this.optionClick.emit (data);
  }
}
interface TableOption {
  mainIcon: string;
  option: Option[];
}
interface Option {
  icon: string;
  class: string;
  label: string;
  event: string;
}

