import { loginUrls ,returnUrl, loginValue } from './../../core/constants/login-constants';
import { StorageService } from 'src/app/core/helper/storage.service';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { environment } from 'src/environments/environment';
import { LoginService } from 'src/app/core/service/login/login.service';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { ToastService } from 'src/app/core/helper/toast.helper.service';


@Component ({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.css']
})
export class LogoutComponent implements OnInit {

  constructor(
    private _storage: StorageService,
    private _loginService: LoginService,
    private _helper: FunctionService,
    private _toast: ToastService,
    private router: Router,
    private _activatedRoute: ActivatedRoute) {
  }

ngOnInit(): void {
    this._loginService.logOut ().subscribe ((res : any) => {
      this._toast.success (res[loginValue.detail]);
      this._helper.deleteCookie ().then (v => {
        this._storage.clearLocalStorage ();
        this.router.navigate ([loginUrls.login],  {queryParams: { returnUrl: this._activatedRoute.snapshot.queryParamMap.get (returnUrl)}});
      });
    });
  }

}
