import { of } from 'rxjs';
import { FunctionService } from './../../helper/function.helper.service';
import { Router } from '@angular/router';
import { ApiEndpoints, LocalStorageKey } from '../../constants/constants';
import { HttpService } from '../common/http.service';
import { Injectable , EventEmitter} from '@angular/core';
import { StorageService } from '../storage/storage.service';
@Injectable ({
  providedIn: 'root'
})
export class AuthenticationService {

  constructor(
    private http: HttpService,
    private _storageService: StorageService,
    private _router: Router,
    private _helper: FunctionService) { }

login(formData) {
    return of ('');
    // return this.http.post(ApiEndpoints.login, formData);
  }

getToken() {
    return localStorage.getItem (LocalStorageKey.userDetails);
  }

getUserId() {
    return JSON.parse (localStorage.getItem (LocalStorageKey.userDetails));
  }

getUserDetails(id) {
    return of ('');
    // return this.http.get(ApiEndpoints.user + `${id}/detail/`);
  }

  forceLogout() {
    this._helper.deleteCookie ().then (v => {
      this._storageService.clearLocalStorage ();
      this._router.navigate (['/login']);
    });
  }
}
