import { Component, forwardRef, OnInit, EventEmitter, Output, Input, ViewChild,ElementRef, Renderer2, OnDestroy } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component ({
  selector: 'app-timeDropdown',
  templateUrl: './timedropdown.component.html',
  styleUrls: ['./timedropdown.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef (() => TimeDropdownComponent),
      multi: true,
    },
  ],
})
export class TimeDropdownComponent implements OnInit , ControlValueAccessor , OnDestroy{
  @Input () time: string;

  @Output () changedValue : EventEmitter<any> = new EventEmitter ();

  @ViewChild ('timePicker',{static: true}) timePicker :ElementRef;

  public height = false;

  public timeList = ['12:00 AM', '12:30 AM', '1:00 AM', '1:30 AM', '2:00 AM', '2:30 AM', '3:00 AM', '3:30 AM', '4:00 AM', '4:30 AM', '5:00 AM', '5:30 AM', '6:00 AM', '6:30 AM', '7:00 AM', '7:30 AM', '8:00 AM', '8:30 AM', '9:00 AM', '9:30 AM', '10:00 AM', '10:30 AM', '11:00 AM', '11:30 AM', '12:00 PM', '12:30 PM', '1:00 PM', '1:30 PM', '2:00 PM', '2:30 PM', '3:00 PM', '3:30 PM', '4:00 PM', '4:30 PM', '5:00 PM', '5:30 PM', '6:00 PM', '6:30 PM', '7:00 PM', '7:30 PM', '8:00 PM', '8:30 PM', '9:00 PM', '9:30 PM', '10:00 PM', '10:30 PM', '11:00 PM', '11:30 PM']

  onTouched = (event: null) => { };

  onChange = (event: any) => { };

  private unListener: () => void;

  constructor( private renderer: Renderer2 ,private elementRef: ElementRef, ) { }

ngOnInit(): void {
    this.unListener = this.renderer.listen ('document', 'mouseup', (event) => {
      const container = this.timePicker.nativeElement;
      if (!container.contains (event.target)) {
         this.height = false;
      }
    });
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  writeValue(obj: any): void {
    this.time = obj;
  }

  valueChanged(data) {
    this.changedValue.emit (data);
  }

  timeClick(time: string){
    this.time = time;
    this.onChange (time);
    this.height = false;
}

  ngOnDestroy(): void {
    this.unListener ();
  }

}
