import { Router, NavigationStart, NavigationEnd } from '@angular/router';
import { Component, OnInit, ChangeDetectionStrategy, ViewEncapsulation, OnDestroy } from '@angular/core';
import { LoaderService } from 'src/app/core/service/loader/loader.service';
import { SubSink } from 'subsink';

@Component ({
  selector: 'app-httploader',
  templateUrl: './httploader.component.html',
  styleUrls: ['./httploader.component.scss'],
  encapsulation: ViewEncapsulation.Emulated

})
export class HttploaderComponent implements OnInit, OnDestroy {
  public showLoader = true;

  private subs = new SubSink ()

  constructor(private _loader: LoaderService) {
    this.subs.add (
      this._loader.isLoading.subscribe (res => {
        this.showLoader = res;
      })
    );
  }

ngOnInit(): void {

  }

  ngOnDestroy(): void {
    this.subs.unsubscribe ();
  }
}
