import { LocalStorageKey } from 'src/app/core/constants/constants';
import { CommonService } from './../../core/service/common/common.service';
import { CommonApiService } from 'src/app/core/service/common/common-api.service';
import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';
declare let $: any;

import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { StorageService } from 'src/app/core/helper/storage.service';
import { environment } from 'src/environments/environment';
import { FunctionService } from 'src/app/core/helper/function.helper.service';

@Component ({
  selector: 'app-full-layout',
  templateUrl: './full.component.html',
  styleUrls: ['./full.component.scss']
})
export class FullComponent implements OnInit {
  public config: PerfectScrollbarConfigInterface = {};

  public filters: any;

  public version: any;

  public sideBarType: string;

constructor(
    public router: Router,
    private _storage: StorageService,
    public _function: FunctionService,
    public _common:  CommonService) {

    this.version = environment.version;
  }

tabStatus = 'justified';

  public isCollapsed = false;

  public innerWidth: any;

  public defaultSidebar: any;

  public showSettings = false;

  public showMobileMenu = false;

  public expandLogo = false;

Logo() {
    this.expandLogo = !this.expandLogo;
  }

  closeMobileView() {
    this.showMobileMenu = false;
  }

  ngOnInit() {
    if (this.router.url === '/') {
      this.router.navigate (['/dashboard']);
    }
    this.defaultSidebar = this._function.layoutOptions.sideBarType;
    this.handleSidebar ();

  }

  /* function for copying the array to filter*/
@HostListener ('window:resize', ['$event'])
  onResize(event) {
    this.handleSidebar ();
  }

handleSidebar() {
    this.innerWidth = window.innerWidth;
    switch (this.defaultSidebar) {
      case 'full':
      case 'iconbar':
        if (this.innerWidth < 1170) {
          this._function.layoutOptions.sideBarType = 'mini-sidebar';
        } else {
          this._function.layoutOptions.sideBarType = this.defaultSidebar;
        }
        break;

      case 'overlay':
        if (this.innerWidth < 767) {
          this._function.layoutOptions.sideBarType = 'mini-sidebar';
        } else {
          this._function.layoutOptions.sideBarType = this.defaultSidebar;
        }
        break;

      default:
    }
  }

toggleSidebarType() {
    switch (this._function.layoutOptions.sideBarType) {
      case 'full':
      case 'iconbar':
        this._function.layoutOptions.sideBarType = 'mini-sidebar';
        break;

      case 'overlay':
        this.showMobileMenu = !this.showMobileMenu;
        break;

      case 'mini-sidebar':
        if (this.defaultSidebar === 'mini-sidebar') {
          this._function.layoutOptions.sideBarType = 'full';
        } else {
          this._function.layoutOptions.sideBarType = this.defaultSidebar;
        }
        break;

      default:
    }
  }

  sideBarTypeSet(event: string) {
    this.sideBarType = event;
  }

  /* for global filter start*/
/* for global filter end*/
  onClickLogo() {
    const user = this._function.getUser;
    // window.location.href = user.client_url + environment.baseVersion;
    window.location.href = user.client_url + '/';
  }

}
