import { AccountModule } from './account/account.module';
import { SidebarComponent } from './shared/sidebar/sidebar.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BlankComponent } from './layouts/blank/blank.component';
import { HttploaderComponent } from './shared/httploader/httploader.component';
import { NavigationComponent } from './shared/header-navigation/navigation.component';
import { FullComponent } from './layouts/full/full.component';
import { AuthHeaderInterceptor } from './core/interceptor/auth-header.interceptor';
import { ErrorInterceptor } from './core/interceptor/error.interceptor';
import { LoaderInterceptor } from './core/interceptor/loader.interceptor';
import { appRoutes } from './app-routing.module';
import { HTTP_INTERCEPTORS, HttpClient } from '@angular/common/http';
import { HttpClientModule } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { CoreModule } from './core/core.module';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatSidenavModule } from '@angular/material/sidenav';
import { StatusCheckInterceptor } from 'src/app/core/interceptor/status-check.interceptor';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar';
import { PERFECT_SCROLLBAR_CONFIG } from 'ngx-perfect-scrollbar';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import {MatIconModule} from '@angular/material/icon';
import { WidgetModule } from './widget/widget.module';
import { SelectDropDownModule } from 'ngx-select-dropdown';
import { ChartModule } from './chart/chart.module';
import { InfiniteScrollModule } from 'ngx-infinite-scroll';

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true,
  wheelSpeed: 1,
  wheelPropagation: true,
  minScrollbarLength: 20
};
class  catchConsoleErrorHandler implements ErrorHandler {
  handleError(error) {
    // do something with the exception
   console.log(error);
  }
}
@NgModule ({
  declarations: [
    AppComponent,
    FullComponent,
    BlankComponent,
    NavigationComponent,
    HttploaderComponent,
    SidebarComponent,

  ],
  imports: [
    BrowserModule,
    MatIconModule,
    BrowserAnimationsModule,
    CoreModule,
    HttpClientModule,
    NgbModule,
    ScrollingModule,
    MatExpansionModule,
    MatSidenavModule,
    FormsModule,
    ReactiveFormsModule,
    PerfectScrollbarModule,
    RouterModule.forRoot (appRoutes, { useHash: false }),
    TranslateModule.forRoot ({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    WidgetModule,
    SelectDropDownModule,
    ChartModule,
    AccountModule,
    InfiniteScrollModule
  ],
  providers: [
    { provide: ErrorHandler, useClass: catchConsoleErrorHandler },
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: LoaderInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: StatusCheckInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthHeaderInterceptor,
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}
export function HttpLoaderFactory(httpClient: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader (httpClient, './assets/i18n/', '.json');
}
