import { Injectable } from '@angular/core';
import { Validators, AbstractControl, ValidationErrors, FormGroup } from '@angular/forms';
import { validationRule } from '../constants/constants';

@Injectable ({
  providedIn: 'root'
})
export class ValidationruleService {

  constructor() { }

  public EMAIL_REGEXP = '^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$';

  public PASSWORD_REGEXP = /(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])/;

  public PASSWORD_REGEXP_2 = /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[*.!#@$%^&(){}[:;<>,.?\'\"`\\/~_+\-=|\]]).{8,}$/;

  public UPPERCASE_LETTERS = /(?=.*[A-Z])/;

  public LOWERCASE_LETTERS = /(?=.*[a-z])/;

  public SPECIAL_CHARACTERS = /(?=.*[*.!#@$%^&(){}[:;<>,.?\'\"`\\/~_+\-=|\]])/;

  public NUMBERS = /(?=.*[0-9])/;

  public time = /^(0?[0-9]|1[0-2]):([0-5]\d)(\s?((?:A|P)\.?M\.?))?$/i;

public textValidation: any = Validators.compose ([
    Validators.required,
    Validators.minLength (validationRule.textMinLength),
    Validators.maxLength (validationRule.textMaxLength)
  ]);

  public mobileValidation: any = Validators.compose ([
    Validators.required,
    Validators.minLength (validationRule.mobileMinLength),
    Validators.maxLength (validationRule.mobileMaxLength)
  ]);

public emailValidation: any = Validators.compose ([
    Validators.required,
    Validators.maxLength (validationRule.emailMaxLength),
    Validators.minLength (validationRule.emailMinLength),
    Validators.email,
    Validators.pattern (new RegExp (this.EMAIL_REGEXP))
  ]);

  public passwordValidation: any = Validators.compose ([
    Validators.required,
    Validators.minLength (validationRule.passwordMinLength),
    Validators.maxLength (validationRule.passwordMaxLength),
    Validators.pattern (new RegExp (this.PASSWORD_REGEXP))
  ]);

  public dateValidation: any = Validators.compose ([
    Validators.required
  ]);

  public radioValidation: any = Validators.compose ([
    Validators.required
  ]);

  public lastNameValidation: any = Validators.compose ([
    Validators.required,

  ]);

  public nameValidation: any = Validators.compose ([
    Validators.required,
    Validators.maxLength (validationRule.nameMaxLength)
  ]);

  public middleNameValidation: any = Validators.compose ([
    Validators.required,

  ]);

  public negativeValidation: any = Validators.compose ([
    Validators.min (0)
  ]);

  public dropDownValidation: any = Validators.compose ([
    Validators.required,
    Validators.minLength (1),

  ]);

  public requiredValidation: any = Validators.compose ([
    Validators.required,
  ]);

  public enhancedPasswordValidation: any = Validators.compose ([
    Validators.required,
    Validators.minLength (8),
    Validators.pattern (new RegExp (this.PASSWORD_REGEXP_2))
  ]);

  public timeValidator: any = Validators.compose ([
    Validators.pattern (new RegExp (this.time))
  ]);

  public textAreaValidation(maxLength: number = validationRule.textAreaMaxLength , minLength: number =  validationRule.textAreaMinLength) {
    return  Validators.compose ([
      Validators.maxLength (maxLength),
      Validators.minLength (minLength),
    ]);
  }

  public textBoxValidation(maxLength: number = validationRule.textMaxLength ,  minLength: number =  validationRule.textMinLength) {
    return  Validators.compose ([
      Validators.maxLength (maxLength),
      Validators.minLength (minLength),
    ]);
  }

  public noWhitespaceValidator(control: AbstractControl): ValidationErrors | null {
    if (typeof control.value === validationRule.string) {
      const isWhitespace = (control.value || '').trim ().length === 0;
      const isValid = !isWhitespace;
      return isValid ? null : { 'whitespace': true };
    }
    return null;
  }

public upperCaseValidator(password: string): boolean {
    const regex = new RegExp (this.UPPERCASE_LETTERS);
    if (regex.test (password)) {
      return true;
    } else {
      return false;
    }
  }

public lowerCaseValidator(password: string): boolean {
    const regex = new RegExp (this.LOWERCASE_LETTERS);
    if (regex.test (password)) {
      return true;
    } else {
      return false;
    }
  }

public specialCharacterValidator(password: string): boolean {
    const regex = new RegExp (this.SPECIAL_CHARACTERS);
    if (regex.test (password)) {
      return true;
    } else {
      return false;
    }
  }

public numberValidator(password: string): boolean {
    const regex = new RegExp (this.NUMBERS);
    if (regex.test (password)) {
      return true;
    } else {
      return false;
    }
  }

public minimumPasswordLength(password: string): boolean {
    if (password.length >= 8) {
      return true;
    } else {
      return false;
    }
  }

public passwordsMatch(controlName: string, matchingControlName: string) {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];
      const matchingControl = formGroup.controls[matchingControlName];

      // set error on matchingControl if validation fails
      if (control.value !== matchingControl.value) {
        matchingControl.setErrors ({ mustMatch: true });
      } else {
        matchingControl.setErrors (null);
      }
    };
  }

setPasswordErrors(controlName: string) {
    return (formGroup: FormGroup) => {
      const control = formGroup.controls[controlName];

      control.setErrors (null);
      // Setup minimum password length error
      if (this.minimumPasswordLength (control.value)) {
        control.setErrors ({ minimumLength: true });
      } else {
        control.setErrors (null);
      }

      // Setup no number error
      if (this.numberValidator (control.value)) {
        control.setErrors ({ noNumber: true });
      } else {
        control.setErrors (null);
      }

      // Setup no upper case letter error
      if (this.upperCaseValidator (control.value)) {
        control.setErrors ({ noUpperCase: true });
      } else {
        control.setErrors (null);
      }

      // Setup no lower case letter error
      if (this.lowerCaseValidator (control.value)) {
        control.setErrors ({ noLowerCase: true });
      } else {
        control.setErrors (null);
      }

      // Setup no special character error
      if (this.specialCharacterValidator (control.value)) {
        control.setErrors ({ noSpecialCharacter: true });
      } else {
        control.setErrors (null);
      }
    };
  }

}
