import { sidebar } from './../../core/constants/constants';
import { SubSink } from 'subsink';
import { Title } from '@angular/platform-browser';
import { ToastService } from 'src/app/core/helper/toast.helper.service';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { StorageService } from './../../core/helper/storage.service';
import { environment } from 'src/environments/environment';
import { Component, AfterViewInit, OnInit, Inject, Input, Output, EventEmitter } from '@angular/core';
import { ROUTES } from './menu-items';
import { SETTINGSROUTES } from './settings-menu-items';
import { RouteInfo } from './sidebar.metadata';
import { Router, ActivatedRoute, NavigationEnd, NavigationStart, NavigationError, RouterEvent } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { filter, map, mergeMap, tap } from 'rxjs/operators';
import { DOCUMENT } from '@angular/common';
import { of } from 'rxjs';
declare let $: any;

@Component ({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  @Input () mobileView = false;

  @Output () mobileClose = new EventEmitter<void> ();

  @Output () sideBarComments = new EventEmitter ();

  showMenu = '';

  showSubMenu = '';

  public sidebarnavItems: any[];

  public mainMenuSelected: any

  public userRole = this._helper.getUser?.user_type;

  public userName = this._helper.getUser?.user_name;

  public isClient: boolean;

  public redirectUri;

  private subs = new SubSink ();

  public customSubMenu: string;

  constructor(
    @Inject (DOCUMENT) private document: Document,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    public _helper: FunctionService
  ) {
    of (this.activatedRoute).pipe (

      map (routeMain => {
        while (routeMain.firstChild) {
          routeMain = routeMain.firstChild;
        }
        return routeMain;
      })
    )
      .pipe (filter (routePrimary => routePrimary.outlet === sidebar.primary))
      .pipe (mergeMap (routeBehaviour => routeBehaviour.data))
      .subscribe (event => {
        this.mainMenuSelected = event.mainMenu;
        this.showMenu = event[ sidebar.mainMenu];
        this.showSubMenu = event[sidebar.title];
      });


    this.router.events
      .pipe (filter (event => event instanceof NavigationEnd))
      .pipe (map (() => this.activatedRoute))
      .pipe (

        map (route => {
          while (route.firstChild) {
            route = route.firstChild;
          }
          return route;
        })
      )
      .pipe (filter (route => route.outlet === 'primary'))
      .pipe (mergeMap (route => route.data))
      .subscribe (event => {
        this.mainMenuSelected = event.mainMenu;
        this.showMenu = event['mainMenu'];
        this.showSubMenu = event['title'];
        this.sideBarSettings ();
      });
  }

// End open close
  ngOnInit() {
    this.isClient = this._helper.getUser.is_client;
    this.sideBarSettings ();
    this.setSubTab ();
  }

// this is for the open close
addExpandClass(element: any, ev) {
  if (ev.submenu && ev.submenu.length) {
    if (element === this.showMenu) {
      this.showMenu = '0';
    } else {
      this.showMenu = element;
    }
  } else {
    if (element === this.showMenu) {
      // this.showMenu = '0';
    } else {
      this.showMenu = element;
    }
  }
}

addActiveClass(element: any) {
  if (element === this.showSubMenu) {
    // this.showSubMenu = '0';
  } else {
    this.showSubMenu = element;
  }
}

checkMainMenu(item, sidebar) {
  if (this.showMenu === item || this.mainMenuSelected === item) {
    return 'selected';
  }
}

  sideBarSettings() {
    this.redirectUri = this.router.url;
    if (this.redirectUri.includes ('/control-panel')) {
      this.sideBarComments.emit ('setting');
      this.sidebarnavItems = SETTINGSROUTES.filter (sidebarnavItem => sidebarnavItem);
    } else {
      this.sideBarComments.emit ('home');
      this.sidebarnavItems = ROUTES.filter (sidebarnavItem => sidebarnavItem);
    }
  }

  setSubTab() {
    this.subs.add (
      this._helper.sidebarSubMenuItem.subscribe (menuItem => {
        this.customSubMenu = menuItem;
      })
    );
  }

getPath(path) {
    if (path === '') {
      return 'javascript:void(0)';
    } else {
      const user = this._helper.getUser;
      const url = path.replace (environment.domain, `${user.domain}`);
      return url;
    }
  }

  close() {
  if ( this.mobileView ){
    this.mobileViewClose () ;
  }
}

mobileViewClose() {
    this.mobileClose.emit ();
    }
}
