import { Component, ElementRef, Input, OnChanges, OnInit, ViewChild } from '@angular/core';
import * as d3 from 'd3';
import { AxisScale, Selection } from 'd3';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { D3BarChartConfiguration, D3BarChartDataFromParent } from 'src/app/core/interface/common.interface';
import { ShortTextPipe } from 'src/app/core/pipe/short-text.pipe';

@Component ({
  selector: 'app-bar-graph',
  templateUrl: './bar-graph.component.html',
  styleUrls: ['./bar-graph.component.scss']
})
export class BarGraphComponent implements OnInit, OnChanges {
  @ViewChild ('barGraph', { static: true }) private chartContainer: ElementRef;

  @ViewChild ('barGraphTooltip', { static: true }) private tooltipContainer: ElementRef;

  @Input () private data: D3BarChartDataFromParent[];

  @Input () private horizontalGraph = false;

  @Input () private chartConfiguration: D3BarChartConfiguration = {
    width: 400,
    height: 210,
    margin: { top: 40, right: 40, bottom: 40, left: 40 },
    axisFontSize: '12px',
    dataLabelFontSize: '12px',
    yAxisTranslateX: -20,
    groupTranslateX: 20
  };

  private svg: Selection<SVGSVGElement | any, any, any, any>;

  private xScale: AxisScale<any>;

  private yScale: AxisScale<any>;

  private xAxis; // Variable to hold the group element added to svg for drawing the X-Axis;

  private yAxis; // Variable to hold the group element added to svg for drawing the Y-Axis;

  private bars; // Variable to hold the selection of all rect elements

  private dataLabels; // Variable to hold the selection of all text elements with the class name 'dataLabels'

  private placeholderDataset = [
    { label: 'No Data 1', colors: '#e9e9e9', data: 1 },
    { label: 'No Data 2', colors: '#e9e9e9', data: 2 },
    { label: 'No Data 3', colors: '#e9e9e9', data: 3 },
    { label: 'No Data 4', colors: '#e9e9e9', data: 4 },
    { label: 'No Data 5', colors: '#e9e9e9', data: 5 }
  ];

constructor(private _shortText: ShortTextPipe, private _helper: FunctionService) { }

ngOnInit(): void {
    this.initSvg ();
    this.initScale (this.placeholderDataset);
    this.drawAxis ();
    this.drawChart ();
  }

ngOnChanges(): void {
    if (this.svg) {
      if (this.data?.length !== 0) {
        this.initScale (this.data);
        this.drawUpdatedAxis ();
        this.updateChart (this.data);
      } else {
        d3.select (this.chartContainer.nativeElement).select ('svg').remove ();
        d3.select (this.tooltipContainer.nativeElement).style ('display', 'none');
        this.initSvg ();
        this.initScale (this.placeholderDataset);
        this.drawAxis ();
        this.drawChart ();
      }
    }
  }

/**
* @description Function used to initialize and create the svg
* @author Sreejith T
* @date 28/09/2021
*/
  initSvg(): void {
    this.svg = d3.select (this.chartContainer.nativeElement)
      .append ('svg')
      .attr ('viewBox', `0 0 ${this.chartConfiguration.width + this.chartConfiguration.margin.left + this.chartConfiguration.margin.right} ${this.chartConfiguration.height + this.chartConfiguration.margin.top + this.chartConfiguration.margin.bottom +10}`)
      .attr ('preserveAspectRatio', 'xMinYMin meet')
      .append ('g')
      .attr ('transform', `translate(${this.chartConfiguration.margin.left + this.chartConfiguration.groupTranslateX} , ${this.chartConfiguration.margin.top})`);
  }

/**
* @description Function used to draw the created axes
* @author Sreejith T
* @date 28/09/2021
*/
  drawAxis(): void {
    this.xAxis = this.svg.append ('g')
      .attr ('class', 'xAxis')
      .attr ('transform', `translate(0, ${this.chartConfiguration.height})`);

    this.yAxis = this.svg.append ('g')
      .attr ('class', 'yAxis')
      .attr ('transform', `translate(${this.chartConfiguration.yAxisTranslateX}, 0)`);

  }

/**
* @description Function used to draw the initial bar chart (placeholder bar graph)
* @author Sreejith T
* @date 28/09/2021
*/
  drawChart(): void {
    this.bars = this.svg
      .selectAll ('rect')
      .data (this.placeholderDataset)
      .enter ()
      .append ('rect')
      .attr ('width', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.xScale (d.data) : this.xScale.bandwidth ())
      .attr ('height', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale.bandwidth () : this.chartConfiguration.height - this.yScale (d.data))
      .attr ('fill', (d: D3BarChartDataFromParent) => d.colors ?  d.colors:  d.color)
      .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : this.xScale (d.label))
      .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale (d.label) : this.yScale (d.data));
  }

/**
* @description Function used to initialize and update the X axis and Y axis from the data provided from the parent component
* @author Sreejith T
* @date 28/09/2021
* @param {D3BarChartDataFromParent[]} data
*/
  initScale(data: D3BarChartDataFromParent[]): void {
    if (!this.horizontalGraph) {
      this.xScale = d3.scaleBand ().domain (data.map (data => data.label)).rangeRound ([0, this.chartConfiguration.width]).padding (0.1);
      this.yScale = d3.scaleLinear ().domain ([0, d3.max (data, (d: D3BarChartDataFromParent) => d.data)]).range ([this.chartConfiguration.height, 0]);
    } else {
      this.xScale = d3.scaleLinear ().domain ([0, d3.max (data, (d: D3BarChartDataFromParent) => d.data)]).range ([0, this.chartConfiguration.width]);
      this.yScale = d3.scaleBand ().domain (data.map (data => data.label)).rangeRound ([this.chartConfiguration.height, 0]).padding (0.1);
    }
  }

/**
* @description Function used to draw the updated axes
* @author Sreejith T
* @date 28/09/2021
*/
  drawUpdatedAxis(): void {
    if (!this.horizontalGraph) {
      this.xAxis.call (d3.axisBottom (this.xScale))
        .selectAll ('text')
        .style ('font-size', this.chartConfiguration.axisFontSize)
        .attr ('text-anchor', 'end')
        .attr ('dx', '-0.3em')
        .attr ('dy', '0.3em')
        .attr ('transform', 'rotate(-30)')
        .text (d => {
          const text = d.split ('-');
          return this._shortText.transform (d, 10);
        });
    }

    this.yAxis.call (d3.axisLeft (this.yScale))
      .selectAll ('text')
      .style ('font-size', this.chartConfiguration.axisFontSize)
      .attr ('text-anchor', 'middle')
      .text (d => {
        if (this.horizontalGraph) {
          const text = d.split ('-');
          return this._shortText.transform (text[1] ? text[1].trim () : text[0].trim (), 7);
        } else {
          return d;
        }
      });
  }

/**
* @description Function used to update the bar chart according to the data provided
* @author Sreejith T
* @date 28/09/2021
* @param {D3BarChartDataFromParent[]} data
*/
  updateChart(data: D3BarChartDataFromParent[]): void {
    this.bars = this.svg.selectAll ('rect').data (data);
    this.dataLabels = this.svg.selectAll ('.dataLabels').data (data);

    this.bars
      .join (
        enter => enter.append ('rect')
          .on ('mouseover', (event: MouseEvent, data: D3BarChartDataFromParent | any) => this.onMouseOver (event, data))
          .on ('mousemove', (event: MouseEvent) => this.onMouseMove (event))
          .on ('mouseout', (event: MouseEvent) => this.onMouseOut (event))
          .attr ('width', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : null)
          .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : null)
          .attr ('height', (d: D3BarChartDataFromParent) => this.horizontalGraph ? null : 0)
          .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? null : this.chartConfiguration.height)
          .transition ()
          .duration (1000)
          .attr ('width', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.xScale (d.data) : this.xScale.bandwidth ())
          .attr ('height', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale.bandwidth () : this.chartConfiguration.height - this.yScale (d.data))
          .attr ('fill', (d: D3BarChartDataFromParent) =>d.colors ?  d.colors:  d.color)
          .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : this.xScale (d.label))
          .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale (d.label) : this.yScale (d.data))
      )
      .on ('mouseover', (event: MouseEvent, data: D3BarChartDataFromParent | any) => this.onMouseOver (event, data))
      .on ('mousemove', (event: MouseEvent) => this.onMouseMove (event))
      .on ('mouseout', (event: MouseEvent) => this.onMouseOut (event))
      .transition ()
      .duration (1000)
      .attr ('width', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.xScale (d.data) : this.xScale.bandwidth ())
      .attr ('height', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale.bandwidth () : this.chartConfiguration.height - this.yScale (d.data))
      .attr ('fill', (d: D3BarChartDataFromParent) => d.colors ?  d.colors:  d.color)
      .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : this.xScale (d.label))
      .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale (d.label) : this.yScale (d.data));

    this.dataLabels
      .join (
        enter => enter.append ('text')
          .on ('mouseover', (event: MouseEvent, data: D3BarChartDataFromParent | any) => this.onMouseOver (event, data))
          .on ('mousemove', (event: MouseEvent) => this.onMouseMove (event))
          .on ('mouseout', (event: MouseEvent) => this.onMouseOut (event))
          .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : null)
          .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? null : this.chartConfiguration.height)
          .transition ()
          .duration (1000)
          .attr ('fill', (d: D3BarChartDataFromParent) => d.colors ?  d.colors:  d.color)
          .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? 0 : this.xScale (d.label))
          .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale (d.label) : this.yScale (d.data))
      )
      .on ('mouseover', (event: MouseEvent, data: D3BarChartDataFromParent | any) => this.onMouseOver (event, data))
      .on ('mousemove', (event: MouseEvent) => this.onMouseMove (event))
      .on ('mouseout', (event: MouseEvent) => this.onMouseOut (event))
      .transition ()
      .duration (1000)
      .attr ('x', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.xScale (d.data) - 13 : this.xScale (d.label) + ((this.xScale.bandwidth ()) / 2))
      .attr ('y', (d: D3BarChartDataFromParent) => this.horizontalGraph ? this.yScale (d.label) + ((this.yScale.bandwidth ()) / 2) : this.yScale (d.data) + 13)
      .text (d => d.data)
      .attr ('text-anchor', 'middle')
      .attr ('dominant-baseline', this.horizontalGraph ? 'central' : 'unset')
      .attr ('class', 'dataLabels')
      .style ('fill', (d: D3BarChartDataFromParent) => d.data === 0 ? '#000' : this._helper.invertColor (d.colors ?  d.colors:  d.color))
      .style ('font-size', this.chartConfiguration.dataLabelFontSize);

  }

/**
 * @description Function used to show the tooltip when is mouse is over on top of the path
 * @author Sreejith T
 * @date 28/09/2021
 * @param {MouseEvent} event
 * @param {D3BarChartDataFromParent} data
 */
  onMouseOver(event: MouseEvent, data: D3BarChartDataFromParent): void {
    d3.select (this.tooltipContainer.nativeElement)
      .html (`<div>
      <label class="tool-tip-text w-100 text-center">${data.label}</label>
      <div class="d-flex align-items-center justify-content-center">
      <span class="tool-tip-marker" style="background-color:${data.colors ?  data.colors:  data.color}"></span>
      <label class="tool-tip-text">${data.data}</label></div>
      </div>`)
      .style ('display', 'flex');
  }

/**
   * @description Function used to move the tooltip according the position of the mouse pointer
   * @author Sreejith T
   * @date 28/09/2021
   * @param {MouseEvent} event
   */
  onMouseMove(event: MouseEvent): void {
        const width = parseInt (d3.select (this.chartContainer.nativeElement).style ('width'));
if ( width/2 < event.offsetX ) {
  d3.select (this.tooltipContainer.nativeElement)
      .style ('top', event.offsetY - 10 + 'px')
      .style ('right',   - event.offsetX  + width + 20 + 'px')
      .style ('left',   'unset');

} else {
      d3.select (this.tooltipContainer.nativeElement)
      .style ('top', event.offsetY - 10 + 'px')
      .style ('left',  event.offsetX + 20 + 'px')
      .style ('right',   'unset');
}

  }

/**
   * @description Function used to hide the tooltip when is mouse is moved out from the path
   * @author Sreejith T
   * @date 28/09/2021
   * @param {MouseEvent} event
   */
  onMouseOut(event: MouseEvent): void {
    d3.select (this.tooltipContainer.nativeElement)
      .html ('')
      .style ('display', 'none');
  }

}
